@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
.nav {
    background-color: var(--shade4);
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: medium;
}

.nav-item a {
    color: var(--shade2);
    font-size: 1.25vw;
}
.footer {
    background-color: var(--shade4);
    bottom: 0;
    padding: 2vh;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
    margin: auto;
}

svg {
    width: 2.75vw;
    margin: 3vh;
}

a svg {
    width: 4vw;
    margin: 3vh;
}
.home {
    color: var(--shade4);
    text-align: center;
    height: 100vh;
    padding-top: 15%;
}

.name {
    display : flex;
    align-items : center;
    padding-left: 20vw;
    font-family: 'Quicksand', sans-serif;
    font-size: 10vw;
    height: 25vh;
    background: var(--shade2);
    text-shadow: -1px -1px 1px rgba(255,255,255,.1), 1px 1px 1px rgba(0,0,0,.5);
}
.about {
    color: var(--shade5);
    font-family: 'Quicksand', sans-serif;
    padding: 5vh;
}

.aboutHead {
    display: flex;
    font-size: 1.25vw;
    margin-bottom: 5vh;
}

.aboutHead img {
    width: 50vh;
    height: auto;
}

.aboutMain {
    padding-left: 3vw;
    padding-right: 2vw;
}

.aboutDetails {
    -webkit-column-count: 3;
            column-count: 3;
    grid-column-gap: 5vw;
    -webkit-column-gap: 5vw;
            column-gap: 5vw;
    -webkit-column-rule: 5px solid var(--shade4);
            column-rule: 5px solid var(--shade4);
    font-size: 1.15vw;
}

.background, .present, .funFacts {
    page-break-inside: avoid;
    -webkit-column-span: 1;
            column-span: 1;
}

.about h1 {
    font-size: 2vw;
    font-weight: bolder;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-bottom: 15px;
    color: var(--shade2);
    background-color: var(--shade4);
}

.background {
    margin-top: -20px;
}

@media screen and (max-width: 820px) { 
    .aboutHead {
        display: block;
        margin-bottom: 3vh;
        text-align: center;
    }
    .aboutHead img {
        width: 30vh;
        height: auto;
        margin-bottom: 1vh;
    }
    .aboutMain {
        padding: 0;
        text-align: left;
    }
    .aboutDetails {
        -webkit-column-count: 1;
                column-count: 1;
    }
}

.portfolio {
    color: var(--shade5);
    font-family: 'Quicksand', sans-serif;
    padding: 5vh;
    text-align: center;
}

.portfolio h1 {
    font-size: 3vw;
    font-weight: bolder;
    text-align: center;
    padding: 1vh;
    color: var(--shade2);
    background-color: var(--shade4);
}

.portfolio img {
    width: 35vh;
    height: auto;
    margin-bottom: 1vh;
}

.gallery {
    -webkit-column-count: 4;
            column-count: 4;
    grid-column-gap: 5vw;
    -webkit-column-gap: 5vw;
            column-gap: 5vw;
    -webkit-column-rule: 3px solid var(--shade1);
            column-rule: 3px solid var(--shade1);
    font-size: 1.15vw;
}

.project {
    page-break-inside: avoid;
    -webkit-column-span: 1;
            column-span: 1;
    margin-bottom: 5vh;
}

.projectTitle {
    font-size: 1.5vw;
    color: var(--shade4);
    -webkit-text-stroke: 0.25px var(--shade5);
    background-color: var(--shade1);
}

.mainProject {
    border: 5vh solid var(--shade1);
    padding: 3vh;
    margin: 5vh auto;
    font-size: 1.75vw;
}

.mainProject img {
    width: 35vw;
    height: auto;
    margin-bottom: 5vh;
}

.mainProject .projectTitle {
    font-size: 3vw;
}

@media screen and (max-width: 820px) { 
    .portfolio {
        padding: 0;
        margin: auto;
    }
    
    .portfolio h1 {
        font-size: 5vw;
        padding: 0;
        margin-bottom: 2vh;
    }

    .portfolio img {
        width: 35vh;
        height: auto;
    }

    .projectTitle {
        font-size: 4vw;
    }

    .gallery {
        -webkit-column-count: 1;
                column-count: 1;
        padding-right: 7vw;
        font-size: 3vw;
    }

    .mainProject {
        border: 1vh solid var(--shade1);
        margin: 2vh;
        padding: 1vh;
        font-size: 3vw;
    }

    .mainProject img {
        margin-bottom: 1vh;
    }

    .mainProject .projectTitle {
        font-size: 4vw;
    }
}
.resume {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
    padding: 5vh;
}

iframe {
    width: 50vw;
    height: 100vh;
}
.contact {
    height: 100vh;
    padding: 5vh;
    text-align: center;
}

.contact h1 {
    color: var(--shade2);
    font-size: 5vh;
    background-color: var(--shade4);
    width: 40vw;
    margin: auto;
    padding: 2vh;
}

form {
    border: 5px solid var(--shade4);
    width: 40vw;
    padding-bottom: 3vh;
    margin: auto;
}

.contact input {
    display: block;
    margin: 5vh auto;
    width: 25vw;
    height: 5vh;
}

.contact button {
    background-color: var(--shade4);
    color: var(--shade2);
    width: 25vw;
    font-size: 3vh;
    padding: 2vh;
    border-radius: 2vh;
}

.error-text {
    padding: 2vh;
    font-size: 1.25vw;
    color: var(--shade5);
}

@media screen and (max-width: 820px) { 
    .contact h1 {
        width: 70vw;
        font-size: 3vh;
    }
    form {
        width: 70vw;
    }
    .contact input {
        width: 60vw;
    }
    .contact button {
        font-size: 2vh;
        width: 45vw;
    }
    
}
/* define color palette */
:root {
    --shade1: #ebb8b1;
    --shade2: #f4e8e1;
    --shade3: #c1c5cc;
    --shade4: #6a859d;
    --shade5: #020201;
    --trans:  #98755400;
}

@-webkit-keyframes animatedBackground {
    0% { background-position: -0 85%; }
    100% { background-position: -1350px 85%; }
}

@keyframes animatedBackground {
    0% { background-position: -0 85%; }
    100% { background-position: -1350px 85%; }
}

@-webkit-keyframes gradient {
	0% { background-position: 50% 0%; }
	50% { background-position: 50% 100%; }
	100% { background-position: 50% 0%; }
}

@keyframes gradient {
	0% { background-position: 50% 0%; }
	50% { background-position: 50% 100%; }
	100% { background-position: 50% 0%; }
}

.base {
	background: linear-gradient(0deg, #f4e8e1, #c1c5cc,  #6a859d);
	background: linear-gradient(0deg, var(--shade2), var(--shade3),  var(--shade4));
	background-size: 400% 400%;
	-webkit-animation: gradient 10s ease infinite;
	        animation: gradient 10s ease infinite;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}
